<template>
  <div style="width: 60%;margin-left: 20%;">
    <div>
      <div>
        <div style="min-width: 333px;">
          <h2>请输入接口信息</h2>
          <el-button @click="showData" class="Third">提交接口</el-button>
          <el-button @click="clearData" class="Third">清空数据</el-button>
          <el-button @click="analyzepara" class="Third">分析参数</el-button>
        </div>
        <div>
          <el-select style="width: 200px;" v-model="queryType" slot="prepend" class="filter-item" @change="changepara">
            <el-option v-for="(item,index) in chooseList"
                       :key="index"
                       :value="item.value"
                       :label="item.label"></el-option>
          </el-select>
          <el-input v-model="appKey" class="half"></el-input>
          <el-input v-model="appSecret" class="half"></el-input>
        </div>
        <div class="get_parameter">
          <ul>
            <li v-for="(item,index) in para" :key="index" :value="item" style="width: 200px;float: left;">
              {{item}}
              <div v-if="ifshow[index]" style="width: 200px;height: 60px;float: left;" >
                <el-input v-model="paramodel[index]" style="width: 180px;" required v-bind:class="{selected: parabool[index]}" v-on:blur="checkinputpara(index)"></el-input>
                <p style="color: red;">{{errortext[index]}}</p>
              </div>
              <div v-if="ifshow1[index]" style="width: 200px;height: 60px;float: left;">
                <template>
                  <el-radio v-model="paramodel[index]" label="0">0</el-radio>
                  <el-radio v-model="paramodel[index]" label="1">1</el-radio>
                </template>
              </div>
            </li>
          </ul>
        </div>
        <div style="width: 70%;">
          <h4>生成接口: </h4>
          <el-input v-model="getinter" style="width: 100%;"></el-input>
          <div style="background-color:#c5c5c5;height: 100px;">{{getinterpara}}</div>
        </div>
        <div class="analyze_parameter">
          <h4>参数分析: </h4>
          <div class="analyze_Data"><p id="Data_para" style="visibility: hidden;color: red;">参数输入有误: {{Datapara.error}}<br/>{{Datapara.list}}</p></div>
        </div>
      </div>
      <div>
        <h2>接口返回结果</h2>
        <div class="return_Data" v-loading="loading1" element-loading-text="拼命加载中"><p>{{Data}}</p></div>
      </div>
    </div>
  </div>
</template>

<script>
let Base64 = require('js-base64').Base64
export default {
  name: 'TimeCloud1',
  data () {
    return {
      appKey: 'b994ec9e4e2b4cdca1631599f602d859',
      appSecret: '1d31a09cb99fe97ba5092e9f559f78f3',
      parabool: [],
      ifshow: [], // 是否显示input或单选框
      ifshow1: [], // 是否显示input或单选框
      errortext: [], // 测试 显示参数分析文字
      loading1: false, // load
      loading2: false, // load
      chooseList: [], // 选择查询条件
      Data: '', // 显示查询原始数据
      queryType: '', // 绑定选择条件
      para: [], // 显示查询参数
      paraType: '', // 查询参数类型
      Datapara: {
        error: '',
        false: [],
        list: ''
      }, // 显示分析参数的结果
      paramodel: [], // 显示查询参数的输入值
      getinter: '', // 生成接口
      getinterpara: {
        imei: ''
      },
      eventcode: '', // 接口数据事件分析
      eventmsg: '' // 接口数据具体信息分析
    }
  },
  methods: {
    checkinputpara (index) {
      if (this.paramodel[index] === undefined) {
        this.parabool.push(true)
        this.parabool[index] = true
        this.errortext[index] = this.para[index] + '输入不能为空'
        console.log('this.parabool[index]=' + this.parabool[index])
        return 0
      }
      console.log(index)
      for (var j = 0; j < this.paraType.length; j++) {
        if (this.para[index] === this.paraType[j].parameter) {
          var pattern = this.getpattern(this.paraType[j]) // 获取正则表达式
          if (pattern.test(this.paramodel[index])) {
            console.log(pattern + '.test(' + this.paramodel[index] + ')=false')
            this.parabool.push(false)
            this.parabool[index] = false
            this.errortext[index] = ''
          } else {
            console.log(pattern + '.test(' + this.paramodel[index] + ')=true')
            this.parabool.push(true)
            this.parabool[index] = true
            this.errortext[index] = this.paraType[j].parameter + '的输入形式应为' + this.paraType[j].type
          }
        }
      }
      this.getinterface()
    },
    getchooseList () {
      this.$http.get('./../static/inter.json').then(res => {
        this.chooseList = res.data.list // 接口+参数
        this.Data = res.data.data // 测试数据
        this.eventcode = res.data.event // 事件代码
        this.eventmsg = res.data.msg // 信息解析
        this.paraType = res.data.parameter // 参数信息
        this.analyzeData()
      })
    }, // 导入查询条件
    showData () {
      this.analyzepara() // 分析参数是否规范
      this.getinterface() // 核实接口
      if (this.Datapara.error !== 0) {
        return this.$message.error('参数有误,详情请见参数分析')
      } // 参数输入有误报错
      let t = this.getinter
      if (!t) {
        return this.$message.error('接口为空，请先生成接口')
      } // 接口为空报错
      // var appKey = 'b994ec9e4e2b4cdca1631599f602d859' // 需要输入和核实
      // var appSecret = '1d31a09cb99fe97ba5092e9f559f78f3' // 需要输入和核实
      var auth = Base64.encode(this.appKey + ':' + this.appSecret) // 加密
      this.loading1 = true // 启动loading
      this.$http({
        timeout: 5000,
        method: 'post',
        url: t,
        headers: {
          Authorization: 'Basic ' + auth
        },
        data: this.getinterpara
      }).then(res => {
        this.Data = res.data // 获取源数据
        this.loading1 = false // 关闭loading
      }).catch(err => {
        if (err.config.timeout === 5000) {
          this.Data = '请求超时'
          this.loading1 = false
        } else {
          this.Data = err
          this.loading1 = false
        }
      })
    }, // 获取查询数据
    clearData () {
      this.para = []
      this.Data = ''
      this.Datapara = {
        error: '',
        false: [],
        list: ''
      }
      this.queryType = ''
      this.getinter = ''
      document.getElementById('Data_para').style.visibility = 'hidden'
    }, // 清空参数 原始数据 解析数据
    changepara () {
      this.paramodel = []
      this.para = []
      this.parabool = []
      this.errortext = []
      for (var i = 0; i < this.chooseList.length; i++) {
        if (this.queryType === this.chooseList[i].value) {
          this.para = this.chooseList[i].parameter.split(',')
          this.paramodel.length = this.para.length // 获取参数
          this.getinter = this.chooseList[i].URL // 获取接口
        }
      }
      for (var m = 0; m < this.para.length; m++) {
        for (var n = 0; n < this.paraType.length; n++) {
          if (this.para[m] === this.paraType[n].parameter) {
            if (this.paraType[n].type === 'bool') {
              this.ifshow[m] = false
              this.ifshow1[m] = true
            } else {
              this.ifshow[m] = true
              this.ifshow1[m] = false
            }
          }
        }
      } // 判断显示为输入框还是单选框
    }, // 改变查询条件 显示参数
    getinterface () {
      this.getinter = ''
      this.getinterpara = {}
      for (var i = 0; i < this.chooseList.length; i++) {
        if (this.queryType === this.chooseList[i].value) {
          this.getinter = this.chooseList[i].URL
        }
      }
      for (var j = 0; j < this.paramodel.length; j++) {
        if (!this.paramodel[j]) { continue }
        this.getinterpara[this.para[j]] = this.paramodel[j]
      }
    }, // 显示接口及接口参数输入值
    analyzepara () {
      this.Datapara.error = 0
      this.Datapara.list = []
      this.Datapara.false = []
      document.getElementById('Data_para').style.visibility = 'visible'
      for (var i = 0; i < this.para.length; i++) {
        for (var j = 0; j < this.paraType.length; j++) {
          if (this.para[i] === this.paraType[j].parameter) {
            var pattern = this.getpattern(this.paraType[j])
            if (this.paraType[j].need === 'TRUE') {
              this.geterror(this.paraType[j], pattern, this.paramodel[i])
            } else {
              if (this.paramodel[i]) {
                this.Datapara.false.push(this.paraType[j].parameter)
                this.geterror(this.paraType[j], pattern, this.paramodel[i])
              }
            }
          }
        }
      }
      if (this.queryType === 'callin') {
        if (this.Datapara.false.length === 1) {
          this.Datapara.error += 0
        } else {
          this.Datapara.error += 1
          this.Datapara.list.push(this.Datapara.error + ': firewall,ring,viberate,nodisturb_time 有且仅有一项')
        }
      }
    }, // 用正则表达式分析参数
    getpattern (parameter) {
      if (parameter.parameter === 'ip') {
        return new RegExp('^(?:(?:2[0-4][0-9]\\.)|(?:25[0-5]\\.)|(?:1[0-9][0-9]\\.)|(?:[1-9][0-9]\\.)|(?:[0-9]\\.)){3}(?:(?:2[0-5][0-5])|(?:25[0-5])|(?:1[0-9][0-9])|(?:[1-9][0-9])|(?:[0-9]))$')
      } else {
        return new RegExp(parameter.RegExp)
      }
    }, // 获取正则表达式
    geterror (parameter, pattern, inputtext) {
      if (inputtext) {
        if (pattern.test(inputtext)) {
          this.Datapara.error += 0
        } else {
          this.Datapara.error += 1
          this.Datapara.list.push(this.Datapara.error + ' : ' + parameter.parameter + '的输入形式应为' + parameter.type + '，描述为' + parameter.describe)
        }
      } else {
        this.Datapara.error += 1
        this.Datapara.list.push(this.Datapara.error + ' : ' + parameter.parameter + '不能为空，' + '描述为' + parameter.describe)
      }
    } // 获取参数错误方式
  },
  created () {
    this.getchooseList()
  }
}
</script>

<style scoped>
  .Third{
    width: 30%;
    min-width: 100px;
    /*margin-left:5px;*/
  }
  .half{
    width: 30%;
    min-width: 330px;
    margin-left:5px;
  }
  .selected{
    border:1px solid red;
    border-radius: 10px;
  }
  div{
    width: 100%;
    float: left;
    border-radius: 10px;
    border: 1px;
    /*min-width: 333px;*/
  }
  li{
    list-style-type: none;
  }
  h4{
    float: left;
  }
  p{
    margin-left: 10px;
  }
  .get_parameter{
    width: 30%;
    min-height: 450px;
    min-width: 270px;
    background-color: RGB(220,230,241);
    margin-top:10px;
  }
  .return_Data{
    background-color: RGB(184,204,228);
    min-height: 600px;
    min-width: 270px;
    white-space: pre;
  }
  .analyze_parameter{
    width: 70%;
  }
  .analyze_Data{
    min-height: 150px;
    min-width: 270px;
    background-color: RGB(184,204,228);
    margin-top:10px;
    white-space: pre;
  }
</style>



